import billCom from '../billCom'
import popupCom from '../commons/popupCom'
import noData from '../commons/exception/noData.vue'

export default {
    name: 'myDealt',
    components: {
        billCom,
        popupCom,
        noData
    },
    data () {
        return {
            ispulled: false,
            pulldownConfig: {
                content: 'Pull Down To Refresh',
                height: 60,
                autoRefresh: false,
                downContent: '下拉刷新',
                upContent: '释放加载',
                loadingContent: '',
                clsPrefix: 'xs-plugin-pulldown-'
            },
            pullupConfig: {
                content: '',
                pullUpHeight: 60,
                height: 40,
                autoRefresh: false,
                downContent: '下拉加载',
                upContent: '',
                // upContent: 'Pull Up To Refresh',
                loadingContent: '',
                // loadingContent: 'Loading...',
                clsPrefix: 'xs-plugin-pullup-'
            },
            bugfix: false,
            bugfix_: true,
            Rendering: true,
            buttoninfo: {
                content: '立即处理',
                status: true
            },
            isSelectall: false,
            bkselectOn: false,
            count: 0,
            billlist: [],
            page_size: 3,
            searchForm: {
                page_size: this.page_size,
                list_type: ''
            },
            ishandle: false,
            pullupEnabled: true,
            status: {
                pullupStatus: 'default',
                pulldownStatus: 'default'
            },
            billinfo: {},
            selectNum: 0,
            selectList: [],
            popupinfo: {}
        }
    },
    async created () {
        // 复原
        await this.clearFilter()
        // 页面初始化 。。。。
        // 获取单据列表
        await this.getList()
        // console.log('搜索',this.$route,this.$route.params.filterinfo)
    },
    computed: {
        isDataLoading () {
            return this.$store.state.isDataLoading
        }
    },
    async mounted () {
        // console.log(this.pulldownConfig())
    },
    watch: {
        async '$route' () {
            // 复原
            await this.clearFilter()
            // 页面初始化 。。。。
            // 获取单据列表
            await this.getList()
            // console.log(this, this.bugfix)
            this.bugfix_ = false
            this.Rendering = false
            this.$nextTick(() => {
                this.bugfix_ = true
                this.Rendering = true
            })
            // await this.$forceUpdate()
            // await this.$children.forEach((item)=>{item.$forceUpdate()})
        }
    },
    methods: {
        // 点击认领
        submit (info, billinfo) {
            this.billinfo = billinfo
            if (billinfo.type === 'WITHDRAW') {
                return
            }
            // 跳转详情页
            let params = {
                // router: this.$route.name
            }
            let query = {
                id: billinfo.id
            }
            params = Object.assign(params, billinfo)
            this.$router.push({
                name: 'detailPage',
                // params,
                query
            })
        },
        // 路由跳转
        routeskip () {
            // 跳转申请页面
            let params = {
                router: this.$route.name
            }
            let query = {
                router: this.$route.name
            }
            this.$router.push({
                name: 'searchPage',
                params,
                query
            })
        },
        cancel () {
            this.ishandle = false
        },
        confirm () {
            this.ishandle = false
        },
        async loadMore () {
            this.bugfix = true
            this.searchForm.page_size += this.page_size
            if (!this.bkselectOn && this.count !== this.billlist.length) {
                await this.getList()
            }
            await this.$refs.scroller.donePullup()
            await this.$refs.scroller.reset()
            this.ispulled = true
        },
        async refresh () {
            // 复原
            await this.clearFilter()
            // 页面初始化 。。。。
            // 获取单据列表
            await this.getList()
            await this.$refs.scroller.donePulldown()
            if (!this.pullupEnabled) {
                await this.$refs.scroller.enablePullup()
            }
        },
        changePullupStatus (enabled) {
            if (enabled) {
                this.$refs.scroller.enablePullup()
                this.pullupEnabled = true
            } else {
                this.$refs.scroller.disablePullup()
                this.pullupEnabled = false
            }
        },
        async clearFilter () {
            this.isSelectall = false
            this.bkselectOn = false
            this.billlist = [...this.billlist]
            this.bugfix = false

            this.searchForm = {
                page_size: this.page_size,
                list_type: ''
            }
            this.buttoninfo = {
                status: true,
                url_ori: this.$route.name
            }
            // console.log(this.$route)
            this.searchForm.list_type = this.$route.name === 'claimBill' ? 'receiving' : 'my_todo'
            // console.log(this.searchForm.list_type)
            this.count = 0
            // this.billlist = []
            this.ishandle = false
            this.billinfo = {}
            this.selectNum = 0
        },
        // 获取列表
        async getList () {
            var vm = this
            let params = {
                page_size: this.searchForm.page_size
            }
            let resUrl = ''
            // 待办
            if (this.$route.name === 'myDealt') {
                params['view_type'] = 'my_todo'
                resUrl = 'getList'
            } else if (this.$route.name === 'processingRecords') {
                // params['operator'] = window.username
                params['view_type'] = 'my_dealt'
                resUrl = 'getLoglist'
            }
            // 搜索//跳转
            if (this.$route.params.filterinfo) {
                params['keyword'] = this.$route.params.filterinfo
            }
            // console.log(params, resUrl)
            this.$store.commit('changeLoadingStatus', true)
            await this.$store.dispatch(resUrl, params).then((res) => {
                this.count = res.data.count || 0
                this.billlist = res.data.items
            }).catch((res) => {
                this.$store.commit('changetoastinfo', {
                    toast_state: 'cancel',
                    // toast_text: '认领失败'
                    toast_text: res.data.msg
                })
            }).finally(() => {
                this.$store.commit('changeLoadingStatus', false)
            })
        }
    }
}



