import qs from 'qs'

export default {
    name: 'file',
    props: {
        item: {
            type: Object,
            default () {
                return {}
            }
        },
        fields: {
            type: Array,
            default () {
                return []
            }
        }
    },
    data () {
        return {
            fileList: [],
            length: 0
        }
    },
    mounted () {
        this.item.value = []
    },
    methods: {
        addFile () {
            this.fileList = this.fileList.splice(0, this.fileList.length)
            this.item.desc = ''
            this.item.value = []
            this.fileList = Object.values(this.$refs.file.files)
            this.fileList.forEach(item => {
                let formData = new FormData()
                formData.append('field_file', item)
                let params = {
                    state_id: this.item.state_id,
                    ticket_id: this.item.ticket_id,
                    key: this.item.key,
                    field_file: formData
                }
                // formData = qs.stringify(formData)
                this.$store.commit('changeLoadingStatus', true)
                this.$store.dispatch('orderProcess/file_upload', params).then((res) => {
                    this.$store.commit('changetoastinfo', {
                        toast_state: 'success',
                        toast_text: '上传成功'
                    })
                    this.length = this.fileList.length
                    this.fileList.forEach(ite => {
                        this.item.desc = this.item.desc + ' ' + ite.name
                        this.item.value.push(ite.name)
                    })
                }).catch((res) => {
                    this.$store.commit('changetoastinfo', {
                        toast_state: 'cancel',
                        toast_text: '上传失败'
                    })
                }).finally(() => {
                    this.$store.commit('changeLoadingStatus', false)
                })
            })
        }
    }
}



