import nodePage from '../nodePage'
import orderProcess from '../orderProcess'
import CollapseTransition from '../../../../pc/src/utils/collapse-transition'

export default {
    name: 'detailPage',
    components: {
        CollapseTransition,
        nodePage,
        orderProcess
    },
    data () {
        return {
            isweixin: window.isweixin,
            isshowstate: false,
            isgdk: false,
            nodeinfo: {},
            count: 3,
            fromInfopages: false,
            billinfo: {},
            handlefo: {},
            stateslist: [],
            isshow: false,
            currentStateIds: []
        }
    },
    async mounted () {
        // 页面初始化 。。。。
        if (!this.$route.query.id) {
            this.$router.back()
            return
        }
        await this.getdetail()
        await this.getNodeList()
    },
    methods: {
        change (item, index) {
            item['isshow'] = !item['isshow']
            this.stateslist.splice(index, 1, item)
            return item
        },
        scrollEvent ($event) {
            // console.log(this.$refs.gdk.offsetTop)
            // console.log($event, $event.target.scrollTop)
            // if (this.$refs.gdk.offsetTop <= $event.target.scrollTop) {
            //     this.isgdk = true
            // }
            // if (this.$refs.gdk.offsetTop >= $event.target.scrollTop) {
            //     this.isgdk = false
            // }
            // this.styletranslateY['transform'] = `translate(-26px,${-$event.target.scrollTop - 36}px)`
        },
        // 处理人栏显示处理
        processtrans (item) {
            switch (item.current_status) {
                case 'DISTRIBUTING':
                    return item.current_assignors
                // break
                case 'DISTRIBUTING-RECEIVING':
                    return (Array.from(new Set([...item.current_processors.split(','), ...item.current_assignors.split(',')])).join().replace(/(^,*)|(,$)/g, ''))
                // break
                default :
                    // return item.current_processors || item.creator || '--'
                    return item.current_processors || '--'
                // break
            }
        },
        routeskip (nodeinfo) {
            let query
            if (this.currentStateIds.indexOf(nodeinfo.id)) {
                return
            }
            // 跳转节点页面
            query = {
                id: this.billinfo.id,
                service: this.billinfo.service,
                state_id: nodeinfo.id
            }
            // query = Object.assign(query, {nodeinfo: nodeinfo})
            this.$router.push({
                name: 'nodePage',
                query
            })
        },
        async getdetail () {
            let queryId = location.hash.split('=')[1]
            let params = {}
            let resUrl = 'getdetail'
            if (this.$route.query.id) {
                params['id'] = this.$route.query.id || queryId
                this.$store.commit('changeLoadingStatus', true)
                await this.$store.dispatch(resUrl, params).then((res) => {
                    this.currentStateIds = res.data.current_steps.map(item => item.id)
                    this.billinfo = res.data
                    this.$route.query['id'] = params['id']
                    this.$route.query['service'] = this.billinfo.service
                }).catch((res) => {
                    this.$store.commit('changetoastinfo', {
                        toast_state: 'cancel',
                        toast_text: res.data.msg
                    })
                }).finally(() => {
                    this.$store.commit('changeLoadingStatus', false)
                })
            }
        },
        async getNodeList () {
            const params = {
                id: this.$route.query.id
            }
            await this.$store.dispatch('getnode', params).then(res => {
                res.data.forEach(state => {
                    state.isshow = false
                    state.isCurrent = false
                    if (this.currentStateIds.indexOf(state.id) !== -1) {
                        state.isCurrent = true
                    }
                })
                this.stateslist = res.data
            }).catch((res) => {
                this.$store.commit('changetoastinfo', {
                    toast_state: 'cancel',
                    toast_text: res.data.msg
                })
            })
        }
    }
}



