
import Popup from 'vux/src/components/popup/index.vue'

import mixins from '../field.js'

export default {
name: 'checkboxMembers',
mixins: [mixins],
components: {
Popup
},
props: {
item: {
type: Object,
default () {
return {}
}
},
fields: {
type: Array,
default () {
return []
}
}
},
data () {
return {
options: [],
selects: []
}
},
watch: {
'item.value' () {
this.conditionField(this.item, this.fields)
}
},
async mounted () {
this.conditionField(this.item, this.fields)
},
methods: {
showPlugin (item) {
this.$parent.showPlugin(item)
},
closeMore (item) {
this.$parent.closeMore(item)
},
submitMore (item) {
this.$parent.submitMore(item)
},
checkNode (item, node) {
node.check = !node.check
const valueList = item.list.filter(x => {
return x.check
})
const valueInfo = []
const valueKey = []
valueList.forEach(x => {
valueInfo.push(x.name)
valueKey.push(x.value)
})
if (item.type !== 'MEMBERS') {
item.value = valueKey.join(',')
item.showValue = valueInfo.join(',')
} else {
item.value = valueInfo.join(',')
item.showValue = valueInfo.join(',')
}
}
}

}



