
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import mixins from '../field.js'

export default {
name: 'selectRadio',
mixins: [mixins],
components: {
PopupPicker
},
props: {
item: {
type: Object,
default () {
return {
value: []
}
}
},
fields: {
type: Array,
default () {
return []
}
}
},
data () {
return {
list: [],
boll: true
}
},
watch: {
'item.value' () {
this.conditionField(this.item, this.fields)
}
},
mounted () {
this.conditionField(this.item, this.fields)
},
methods: {
getValue (val) {
this.item.value = this.item.list.filter(ite => {
return ite.name === val
})[0].name
}
}

}



