export default (Vue) => {
    // 自定义 外点击/触发
    Vue.directive('clickOut', {
        bind: async function (el, binding) {
            // console.log('绑定')
            let cascaderFunction = ''
            let documentHandler = await async function (e) {
                if (!el.contains(e.target)) {
                    if (!cascaderFunction) {
                        cascaderFunction = binding.value
                    } else {
                        // console.log('外点击/移除---', binding.value)
                        await binding.value()
                        await document.removeEventListener('click', documentHandler)
                    }
                } else {
                    // console.log('内点击')
                }
            }
            await document.addEventListener('click', documentHandler)
        },
        inserted: function (el, binding, vnode) {
        },
        update: async function (el, binding, vnode, oldVnode) {
            // console.log('更新绑定')
            let cascaderFunction = ''
            let documentHandler = await async function (e) {
                if (!el.contains(e.target)) {
                    if (!cascaderFunction) {
                        cascaderFunction = binding.value
                    } else {
                        // console.log('更新外点击/移除---', binding.value)
                        await binding.value()
                        await document.removeEventListener('click', documentHandler)
                    }
                } else {
                    console.log('内点击')
                }
            }
            await document.addEventListener('click', documentHandler)
        }
    })
    // 绑定值为True时 --》 html元素获取焦点
    Vue.directive('focus', {
        // inserted: function (el) {
        //     el.focus()
        // },
        update: function (el, {value}) {
            if (value) {
                el.focus()
            }
        }
        // update (el, binding) {
        //     binding.value && el.focus()
        // }
    })
    // 添加自定义锚点 自动滚动到锚点 ??
    Vue.directive('anchor', {
        bind: function (el, binding, vnode) {
            // 自定义属性：
            // el.setAttribute("showme",false)
            binding.value.el = el
        },
        update: function (el, binding, vnode, oldVnode) {
        },
        componentUpdated: function (el, binding, vnode, oldVnode) {
        }
    })
    Vue.directive('scrollFocus', {
        // 当被绑定的元素插入到 DOM 中时……
        inserted: function (el, binding) {
            // 聚焦元素
            if (!binding.value) {
                el.scrollIntoView(true)
            }
        },
        update: function (el, binding) {
            // 聚焦元素
            if (!binding.value) {
                el.scrollIntoView(true)
            }
        }
    })
}



