import Vue from 'vue'
import ajax from '../../utils/ajax'
import bus from '../../utils/bus'

export default {
    namespaced: true,
    state: {

    },
    mutations: {

    },
    actions: {
        get_data_by_key ({ commit, state, dispatch }, params) {
            return ajax.get(`${window.site}datadicts/get_data_by_key/`, { params: params }).then(response => {
                let res = response.data
                return res
            })
        },
        // 获取级联字段数据源 （工单）
        get_api_data ({ commit, state, dispatch }, params) {
            return ajax.post(`${window.site}` + `fields/${params.id}/api_field_choices/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 获取级联字段数据源 （工单）
        get_data_workflow ({ commit, state, dispatch }, params) {
            return ajax.post(`${window.site}` + `workflow/fields/${params.id}/api_field_choices/`, params).then(response => {
                let res = response.data
                return res
            })
        }
    }
}



