/**
 * store 数据管理
 */

import Vue from 'vue'
import Vuex from 'vuex'

import orderProcess from './modules/orderProcess'
import common from './modules/common'
import ajax from '../utils/ajax'
import apiField from './modules/api_field'

Vue.use(Vuex)

export default new Vuex.Store({
    // 模块
    modules: {
        orderProcess,
        common,
        apiField
    },
    // 公共 store
    state: {
        isDataLoading: false,
        toastinfo: {
            toast_show: false,
            toast_state: 'success',
            toast_text: ''
        },
        onshow: function () {
        }
    },
    // 公共 mutations
    mutations: {
        // loading状态
        changeLoadingStatus (state, value) {
            state.isDataLoading = value
        },
        // toastinfo状态
        changetoastinfo (state, value) {
            state.toastinfo.toast_state = value.toast_state
            state.toastinfo.toast_text = value.toast_text
            state.toastinfo.toast_show = true
        }
    },
    // 公共 actions
    actions: {
        getList ({commit, state, dispatch}, params) {
            return ajax.get(`${window.site}ticket/`, {params: params}).then(response => {
                let res = response.data
                return res
            })
        },
        getLoglist ({commit, state, dispatch}, params) {
            return ajax.get(`${window.site}ticket/`, {params: params}).then(response => {
                let res = response.data
                return res
            })
        },
        getdetail ({commit, state, dispatch}, params) {
            return ajax.get(`${window.site}ticket/${params.id}/`, {params: params}).then(response => {
                let res = response.data
                return res
            })
        },
        getnode ({commit, state, dispatch}, params) {
            return ajax.get(`${window.site}ticket/${params.id}/states/`, {params: params}).then(response => {
                let res = response.data
                return res
            })
        },
        // 认领单据
        claimBill ({commit, state, dispatch}, params) {
            return ajax.post(`${window.site}ticket/claim_ticket/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        changdate ({commit, state, dispatch}, value) {
            // let date = new Date('2014-04-23 18:55:49:123')
            let dateList = value.split(' ')
            // console.log(dateList)
            let date = new Date(dateList[0])
            // console.log(date)
            let currentTime = new Date()
            if (date.getTime() + 24 * 60 * 60 * 1000 > currentTime) {
                // console.log(dateList[1])
                return dateList[1]
            } else {
                // console.log(dateList[0])
                return dateList[0]
            }
        }
    },
    // 公共 getters
    getters: {

    }
})



