import {bus} from './utils/bus'
import prompt from './views/commons/prompt.vue'

export default {
    name: 'app',
    components: {
        prompt
    },
    data () {
        return {
            isShade: false,
            hasProjectPermission: true,
            title: 'ITSM',
            tabbarlist: [
                {
                    badge: '待办列表',
                    link: '/my_dealt',
                    selected: false
                },
                {
                    badge: '处理记录',
                    link: '/processing_records',
                    selected: false
                }
            ],
            isweixin: window.isweixin,
            routerKey: +new Date()
        }
    },
    computed: {

    },
    watch: {
        '$route' () {
            this.initdata()
        }
    },
    async created () {
        this.initdata()
    },
    methods: {
        initdata () {
            let pathstr = JSON.parse(JSON.stringify(this.$route.path))
            this.tabbarlist.forEach(element => {
                element['selected'] = element.link === pathstr
            })
            if (pathstr === '/') {
                this.tabbarlist[0]['selected'] = true
            }
        }
    },
    mounted () {
        // console.log(bus)
    }
}



