import Vue from 'vue'
import ajax from '../../utils/ajax'
import bus from '../../utils/bus'

export default {
    namespaced: true,
    state: {

    },
    mutations: {

    },
    actions: {
        // 获取单据信息
        // window.site + `download_file/?state_id=${statedId}&ticket_id=${tickedId}&key=${item.key}&file_name=${name}`
        //         window.open(this.downFileUrl)
        getFile ({commit, state, dispatch}, params) {
            return ajax.get(`${window.site}misc/download_file/`, {params: params}).then(response => {
                let res = response
                return res
            })
        }
    }
}



