export default {
    name: 'exception',
    props: {

    },
    data () {
        return {
            message: '抱歉！您访问的页面出现异常…'
        }
    },
    mounted () {
        if (String(this.$route.query.msg) === '439') {
            this.message = '抱歉！您无权查看该工单或该工单已被他人处理'
        }
    },
    methods: {
        backHome () {
            this.$router.push({name: 'myDealt'})
        }
    }
}



