export default {
    name: 'exception',
    props: {
        isSearch: {
            type: Boolean,
            default () {
                return false
            }
        }
    },
    data () {
        return {
            message: ''
        }
    },
    mounted () {
        if (String(this.$route.name) === 'myDealt') {
            this.message = '效率很高哟，暂无待办'
        } else if (String(this.$route.name) === 'claimBill') {
            this.message = '暂无待认领'
        } else if (String(this.$route.name) === 'processingRecords') {
            this.message = '暂无处理记录'
        }
    },
    created () {

    }
}



