
import mixins from '../field.js'
import Popup from 'vux/src/components/popup/index.vue'


export default {
name: 'treeSelect',
mixins: [mixins],
components: {
Popup
},
props: {
item: {
type: Object,
default () {
return {}
}
},
fields: {
type: Array,
default () {
return []
}
}
},
data () {
return {}
},
watch: {
'item.value' () {
this.conditionField(this.item, this.fields)
}
},
mounted () {
this.conditionField(this.item, this.fields)
},
methods: {
showPlugin (item) {
this.$parent.showPlugin(item)
},
closeMore (item) {
this.$parent.closeMore(item)
},
submitMore (item) {
this.$parent.submitMore(item)
},
checkTreeNode (item, node) {
item.valueName = node.name
item.value = node.id
item.list.forEach(x => {
x.check = false
})
node.check = !node.check
item.secondChildren = []
item.thirdChildren = []
if (node.children && node.children.length) {
item.secondChildren = []
node.children.forEach(x => {
item.secondChildren.push({
name: x.name,
check: false,
children: x.children,
id: x.id
})
})
}
},
checkSecondTree (item, node) {
item.valueName = node.name
item.value = node.id
item.secondChildren.forEach(x => {
x.check = false
})
node.check = !node.check
item.thirdChildren = []
if (node.children && node.children.length) {
item.thirdChildren = []
node.children.forEach(x => {
item.thirdChildren.push({
name: x.name,
check: false,
children: x.children,
id: x.id
})
})
}
},
checkThirdTree (item, node) {
item.valueName = node.name
item.value = node.id
item.thirdChildren.forEach(x => {
x.check = false
})
node.check = !node.check
}
}
}



