/**
 * @desc router 配置
 * @example 路由组件名统一首字母大写
 */

import Vue from 'vue'
import Router from 'vue-router'
import OrderProcess from '../views/orderProcess'
import claimBill from '../views/tab/claim_bill'
import detailPage from '../views/detailPage'
import nodePage from '../views/nodePage'
import searchPage from '../views/searchPage'
import error from '../views/commons/exception/error.vue'
Vue.use(Router)

const routes = [
    // 首页/代办
    {
        path: '/',
        redirect: 'my_dealt'
    },
    // 首页/代办
    {
        path: '/my_dealt',
        name: 'myDealt',
        component: claimBill
    },
    {
        path: '/detail_page',
        name: 'detailPage',
        component: detailPage
    },
    {
        path: '/node_page',
        name: 'nodePage',
        component: nodePage
    },
    {
        path: '/search_page',
        name: 'searchPage',
        component: searchPage
    },
    {
        path: '/processing_records',
        name: 'processingRecords',
        component: claimBill
    },
    {
        path: '/orderProcess',
        name: 'orderProcess',
        component: OrderProcess
    },
    {
        path: '/error',
        name: 'error',
        component: error
    }
]

const router = new Router({
    mode: 'hash',
    routes: routes
})

router.beforeEach((to, from, next) => {
    next()
})

router.afterEach(route => {
})

export default router



