import mixins from '../field.js'

export default {
    name: 'dateDatetime',
    mixins: [mixins],
    props: {
        item: {
            type: Object,
            default () {
                return {}
            }
        },
        fields: {
            type: Array,
            default () {
                return []
            }
        }
    },
    data () {
        return {}
    },
    watch: {
        'item.value' () {
            this.conditionField(this.item, this.fields)
        }
    },
    mounted () {
        this.conditionField(this.item, this.fields)
    },
    methods: {
        showPlugin (item) {
            this.$parent.showPlugin(item)
        }
    }
}



