import Vue from 'vue'
import ajax from '../../utils/ajax'
import bus from '../../utils/bus'

export default {
    namespaced: true,
    state: {},
    mutations: {},
    actions: {
        // 获取单据信息
        getOrderInfo ({commit, state, dispatch}, id) {
            return ajax.get(`${window.site}ticket/${id}/`).then(response => {
                let res = response.data
                return res
            })
        },
        // 获取节点操作
        getNodeOperat ({commit, state, dispatch}, {params, id}) {
            return ajax.get(`${window.site}ticket/${id}/transitions/`, {params: params}).then(response => {
                let res = response.data
                return res
            })
        },
        // 获取所有人员
        getAllUser ({commit, state, dispatch}) {
            return ajax.get(`${window.site}gateway/bk_login/get_all_users/`).then(response => {
                let res = response.data
                return res
            })
        },
        // 获取组成员
        getGroupPerson ({commit, state, dispatch}, params) {
            return ajax.get(`${window.site}users/`, {params: params}).then(response => {
                let res = response.data
                return res
            })
        },
        // 获取字段
        getFields ({commit, state, dispatch}, params) {
            return ajax.get(`${window.site}fields/`, {params: params}).then(response => {
                let res = response.data
                return res
            })
        },
        // 通过 新
        proceedOrder ({commit, state, dispatch}, {id, params}) {
            return ajax.post(`${window.site}ticket/${id}/proceed/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 转单，挂起，恢复，分派，认领 新
        distributeOrder ({commit, state, dispatch}, {params, id}) {
            return ajax.post(`${window.site}ticket/${id}/operate/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 终止 新
        terminableOrder ({commit, state, dispatch}, {params, id}) {
            return ajax.post(`${window.site}ticket/${id}/terminate/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 认领 --
        claimInfo ({commit, state, dispatch}, params) {
            return ajax.post(`${window.site}ticket/claim_ticket/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 分派
        dispatchInfo ({commit, state, dispatch}, params) {
            return ajax.post(`${window.site}ticket/distribute_ticket/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 挂起 --
        hangeInfo ({commit, state, dispatch}, {params, id}) {
            return ajax.post(`${window.site}ticket/${id}/suspend/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 恢复 --
        restoreInfo ({commit, state, dispatch}, {params, id}) {
            return ajax.post(`${window.site}ticket/${id}/unsuspend/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 督办
        superviseInfo ({commit, state, dispatch}, id) {
            return ajax.post(`${window.site}ticket/${id}/supervise/`).then(response => {
                let res = response.data
                return res
            })
        },
        // 转单
        deliverInfo ({commit, state, dispatch}, params) {
            return ajax.post(`${window.site}ticket/${params.ticket_id}/deliver/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 终止
        terminateInfo ({commit, state, dispatch}, params) {
            return ajax.post(`${window.site}ticket/${params.ticket_id}/terminate/`, params).then(response => {
                let res = response.data
                return res
            })
        },
        // 文件上传
        file_upload ({commit, state, dispatch}, params) {
            return ajax.post(`${window.site}misc/upload_file/?state_id=${params.state_id}&ticket_id=${params.ticket_id}&key=${params.key}`, params.field_file).then(response => {
                let res = response.data
                return res
            })
        }
    }
}



