export default {
    name: 'billCom',
    data () {
        return {
            // 通过绿色，驳回红色，转单分派蓝色,撤单灰色
            OPERATE_TYPE: {
                WITHDRAW: '撤单',
                TERMINATE: '终止',
                TRANSITION: '通过',
                // SUSPEND_OPERATE: '被挂起',
                SUSPEND: '挂起',
                // UNSUSPEND_OPERATE: '恢复',
                UNSUSPEND: '恢复挂起',
                DELIVER: '转单',
                // CLAIM: '认领工单',
                CLAIM: '认领',
                // DISTRIBUTE: '分派工单',
                DISTRIBUTE: '分派',
                FOLLOW: '关注',
                FINISHED: '已完成',
                REPULSE: '打回'
            },
            greenList: ['TRANSITION'],
            redList: ['TERMINATE', 'SUSPEND', 'REPULSE'],
            blueList: ['UNSUSPEND', 'DELIVER', 'CLAIM', 'DISTRIBUTE', 'FOLLOW'],
            grayList: ['WITHDRAW', 'FINISHED']
        }
    },
    props: {
        billinfoOri: {
            type: Object,
            default () {
                return {}
            }
        },
        buttoninfoOri: {
            type: Object,
            default () {
                return {
                    content: '',
                    status: ''
                }
            }
        },
        // routeName: String,
        submit: Function
    },
    computed: {
        // billinfo () {
        //     let billinfo
        //     return billinfo
        // }
    },
    async mounted () {
        this.changedate()
    },
    methods: {
        async changedate () {
            let value = this.$route.name === 'processingRecords' ? this.billinfoOri.operate_at : this.billinfoOri.create_at
            this.timet = value
            // await this.$store.dispatch('changdate', value).then((res) => {
            //     // console.log(res)
            //     this.timet = res
            // })
        },
        isColor (type) {
            if (this.greenList.indexOf(type) !== -1) {
                return 'green'
            } else if (this.redList.indexOf(type) !== -1) {
                return 'red'
            } else if (this.grayList.indexOf(type) !== -1) {
                return 'gray'
            } else {
                return 'blue'
            }
        },
        cutmessage (message) {
            if (!message) {
                return ''
            }
            // console.log(message.indexOf('【'))
            // message.split('【')[1].split('】')[0]
            if (message.indexOf('【') !== -1) {
                let ems = message.split('【')[1]
                ems = ems.split('】')[0]
                return ems
            } else {
                return ''
            }
        }
        // submit(){
        //     console.log('提交认领')
        //     // window.confirm('111')
        //     // console.log(this.$parent.$parent)
        //     this.$parent.$parent.ishandle = true
        //     // console.log(this.$parent.$parent.ishandle)
        // }
    }
}



