export default {
    name: 'search',
    data () {
        return {
            itsmWxHistory: window.localStorage.getItem('itsmWxHistory') ? JSON.parse(window.localStorage.getItem('itsmWxHistory')) : [],
            isFilter: false,
            ishandle: false,
            inputinfo: '',
            history_max: 6
        }
    },
    created () {
        // // 页面初始化 。。。。
        if (!this.$route.params.router && !this.$route.query.router) {
            this.$router.back()
        }
    },
    computed: {
    },
    async mounted () {
    },
    methods: {
        clearinput () {
            // console.log('000000000000')
            this.inputinfo = ''
        },
        searching () {
            if (!this.inputinfo) { return }
            let itsmWxHistory = [...this.itsmWxHistory]
            let index = itsmWxHistory.indexOf(this.inputinfo)
            if (index !== -1) {
                itsmWxHistory.splice(index, 1)
            }
            itsmWxHistory.splice(0, 0, this.inputinfo)
            itsmWxHistory = itsmWxHistory.slice(0, this.history_max)
            window.localStorage.setItem('itsmWxHistory', JSON.stringify(itsmWxHistory))
            // 跳转申请页面
            let params = {
                filterinfo: this.inputinfo
            }
            this.$router.push({name: this.$route.params.router || this.$route.query.router,
                params
            })
        },
        qucikSearching (item) {
            this.inputinfo = item
            this.searching()
        },
        clearHistory () {
            this.itsmWxHistory = []
            // window.localStorage.removeItem('itsmWxHistory')
            window.localStorage.setItem('itsmWxHistory', [])
            // // 移除所有
            // window.localStorage.clear();
        }
    }
}



