import _ from 'lodash'

export default {
    props: {},
    data () {
    },
    created () {
    },
    watch: {},
    methods: {
        // 获取数据字典 get_data_by_key
        async get_data_by_key (itemRelate) {
            let vm = this
            let type = ''
            if (itemRelate.type === 'TREESELECT') {
                type = 'tree'
            } else {
                type = 'list'
            }
            await this.$store.dispatch('apiField/get_data_by_key', {
                key: itemRelate.source_uri,
                view_type: type
            }).then(res => {
                let choice = res.data.map(item => {
                    return {
                        name: item.name,
                        check: false,
                        children: item.children,
                        id: item.id,
                        value: itemRelate === 'TREESELECT' ? item.id.toString() : item.key
                    }
                })
                itemRelate.list.splice(0, itemRelate.list.length)
                choice.forEach(itemRefresh => {
                    itemRelate.list.push(JSON.parse(JSON.stringify(itemRefresh)))
                })
            }).catch(res => {
                this.$store.commit('changetoastinfo', {
                    toast_state: 'text',
                    toast_text: '接口请求失败，请联系管理员'
                })
            }).finally({})
        },
        // 获取级联字段 get_data_workflow
        async apiRemoteGetData (params, itemRelate, type) {
            let reqParams = params
            let url = 'apiField/get_api_data'
            await this.$store.dispatch(url, reqParams).then(res => {
                let choice = res.data.map(item => {
                    return {
                        name: item.name,
                        check: false,
                        value: item.key
                    }
                })
                itemRelate.list.splice(0, itemRelate.list.length)
                choice.forEach(itemRefresh => {
                    itemRelate.list.push(JSON.parse(JSON.stringify(itemRefresh)))
                })
            }).catch(res => {
                // this.$store.commit('changetoastinfo', {
                //     toast_state: 'text',
                //     toast_text: '接口请求失败，请联系管理员'
                // })
            })
        },
        debounce: _.debounce(async function (params, itemRelate, vm, type) {
            vm.apiRemoteGetData(params, itemRelate, type)
        }, 1000, {
            'leading': true,
            'trailing': true,
            'maxWait': 2000
        }),
        async isNecessaryToWatch (item, type) {
            if (item.id) {
                // 数据字典
                if (item.fields && item.fields.length) {
                    item.fields.filter(
                        ite => {
                            return ite.source_type === 'DATADICT'
                        }
                    ).forEach(async itemRelate => {
                        itemRelate.choice.splice(0, itemRelate.choice.length)
                        await this.get_data_by_key(itemRelate)
                    })
                }

                // 所有api字段first赋值
                const keyList = item.fields.map(itemfiter => {
                    return itemfiter.key
                })
                if (item.fields && item.fields.length) {
                    item.fields.filter(
                        ite => {
                            return ite.source_type === 'API'
                        }
                    ).forEach(itemRelate => {
                        const params = {
                            id: itemRelate.id
                        }
                        if (itemRelate.related_fields && itemRelate.related_fields.rely_on) {
                            itemRelate.related_fields.rely_on.forEach(itefinal => {
                                let relateobj = item.fields.filter(
                                    itemFi => {
                                        return itemFi.key === itefinal
                                    }
                                )[0]
                                if (keyList.indexOf(itefinal) !== -1) {
                                    params[itefinal] = relateobj ? (relateobj.value || '') : ''
                                }
                            })
                        }
                        // 没有引用变量的api字段 才可以 发送请求
                        if (Object.values(params).every(
                            itemParams => itemParams
                        )) {
                            itemRelate.choice.splice(0, itemRelate.choice.length)
                            this.apiRemoteGetData(params, itemRelate, type)
                        }
                    })
                }
                // 1.当前节点是否有引用变量的api字段
                let CurrentApiFields = item.fields.filter(ite => {
                    return (ite.source_type === 'API') &&
                        ite.related_fields && ite.related_fields.rely_on &&
                        ite.related_fields.rely_on.length
                })
                if (!CurrentApiFields.length) {
                    return
                }
                let relyOnFieldsKeyList = [] // api字段所依赖字段（全部）
                CurrentApiFields.forEach(ite => {
                    relyOnFieldsKeyList = relyOnFieldsKeyList.concat(ite.related_fields.rely_on)
                })
                // 2.当前节点是否有被引用的字段
                let CurrentreBeReliedFields = item.fields.filter(ite => {
                    return ite.related_fields && ite.related_fields.be_relied &&
                        ite.related_fields.be_relied.length
                })
                if (!CurrentreBeReliedFields.length) {
                    return
                }
                // 3.当前节点是否有需要监听的 被引用字段 -- 和当前节点的api字段有关联
                let CurrentrelyOnFields = CurrentreBeReliedFields.filter(ite => {
                    return relyOnFieldsKeyList.indexOf(ite.key) !== -1
                })
                if (!CurrentrelyOnFields.length) {
                    return
                }
                // 4.监听依赖字段 -- api字段所依赖字段（在当前节点的），是否全部填充
                // 监听每个值 // 函数
                let vm = this

                CurrentrelyOnFields.forEach(ite => {
                    vm.$watch(
                        function () {
                            return ite.value
                        },
                        function (newVal, oldVal) {
                            // 关联字段 赋值
                            if (ite.value) {
                                // 相关api字段
                                let rca = CurrentApiFields.filter(item_ => {
                                    return item_.related_fields.rely_on.indexOf(ite.key) !== -1
                                })
                                rca.forEach(async itemRelate => {
                                    // api字段相关依赖
                                    let relateCurrentreBeRelied = CurrentrelyOnFields.filter(
                                        itemRe => {
                                            return itemRelate.related_fields.rely_on.indexOf(itemRe.key) !== -1
                                        }
                                    )
                                    // 在当前节点的依赖字段 是否全部填充 / isALlFill
                                    let isALlFill = relateCurrentreBeRelied.every(
                                        itemRely => {
                                            return itemRely.value
                                        }
                                    )
                                    // 是否全部填充 / 非必填 ？
                                    if (isALlFill || false) {
                                        // console.log('hello, ', newVal, oldVal, itemRelate)
                                        const params = {
                                            id: itemRelate.id
                                        }
                                        if (itemRelate.related_fields && itemRelate.related_fields.rely_on) {
                                            itemRelate.related_fields.rely_on.forEach(
                                                itefinal => {
                                                    let relateobj = CurrentrelyOnFields.filter(
                                                        itemFi => {
                                                            return itemFi.key === itefinal
                                                        }
                                                    )[0]
                                                    if (keyList.indexOf(itefinal) !== -1) {
                                                        params[itefinal] = relateobj ? (relateobj.value || '') : ''
                                                    }
                                                }
                                            )
                                        }
                                        itemRelate.choice.splice(0, itemRelate.choice.length)
                                        itemRelate.value = []
                                        vm.debounce(params, itemRelate, vm, type)
                                    }
                                })
                            }
                        },
                        {
                            deep: true
                        }
                    )
                })
            }
        },
        // 刷新数据源
        freshApi (item, changeFields, type) {
            let vm = this
            const params = {}
            const keyList = changeFields.map(
                itemfiter => {
                    return itemfiter.key
                }
            )
            if (item.related_fields && item.related_fields.rely_on) {
                item.related_fields.rely_on.forEach(
                    itefinal => {
                        let relateobj = changeFields.filter( // CurrentrelyOnFields
                            itemFi => {
                                return itemFi.key === itefinal
                            }
                        )[0]
                        if (keyList.indexOf(itefinal) !== -1) {
                            params[itefinal] = relateobj ? (relateobj.value || '') : ''
                        }
                        // params[itefinal] = relateobj ? relateobj.value : ''
                    }
                )
            }
            // 是否全部填充 / 非必填 ？
            if (Object.values(params).every((item) => {
                return !!item
            }) || false) {
                params['id'] = item.id
                item.choice.splice(0, item.choice.length)
                item.value = ''
                vm.debounce(params, item, vm, type)
            }
        }
    }
}



