
import Datetime from 'vux/src/components/datetime/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import permissions from '../commons/exception/permissions.vue'
import selectRadio from '../commons/fields/selectRadio'
import checkboxMembers from '../commons/fields/checkboxMembers'
import intSring from '../commons/fields/intString'
import dateDatetime from '../commons/fields/dateDatetime'
import textCom from '../commons/fields/text'
import treeSelect from '../commons/fields/treeSelect'
import file from '../commons/fields/file'

import apiFieldsWatch from '../commons/api_fields_watch'
import field from '../commons/field'

export default {
components: {
treeSelect,
PopupPicker,
Datetime,
Popup,
Icon,
permissions,
selectRadio,
checkboxMembers,
intSring,
dateDatetime,
textCom,
file
},
mixins: [apiFieldsWatch, field],
props: {
ticketId: {
type: Number,
default: null
},
billinfo: {
type: Object,
default () {
return {}
}
},
statesList: {
type: Array,
default () {
return []
}
}
},
data () {
return {
clickSencond: false,
listInfo: [],
formInfo: {
currentState: [],
operat: [],
person: ['请选择'],
startTime: ['2019-12-21'],
endTime: '2019-03-12'
},

contentInfo: {

operatValue: '',
operatList: [[]],

placeholder: '',

backValue: [],

deliverList: [],
deliverShow: false,
deliver_to: '',
deliver_name: '',
deliverCheck: false,
dispatchType: '',

groupList: [
{
id: 1,
desc: 'CMDB业务公用角色',
name: 'CMDB业务公用角色',
type: 'CMDB',
check: false
},
{
id: 2,
desc: '通用角色表',
name: '通用角色表',
type: 'GENERAL',
check: false
},
{
id: 5,
desc: '个人',
name: '个人',
type: 'PERSON',
check: false
}
],
groupShow: true,

dataMore: '',

reason: '',
reasonCheck: false
},

personList: [],
searchValue: '',
toastInfo: '',
currentList: [[]],
handling: {},
operationPlace: '请选择要操作的节点'
}
},
watch: {
'formInfo.operat' (val) {
this.changeForm()
this.changeOperat(val)
},

'formInfo.currentState' (val) {
this.getOperate(val)
},
'statesList' () {
this.initData()
},
'contentInfo.deliverShow' (val) {
if (!val) {
this.searchValue = ''
this.contentInfo.groupShow = true
} else {
for (let i = 0; i < this.contentInfo.deliverList.length; i++) {
this.contentInfo.deliverList[i].show = true
}
}
},

searchValue () {

this.contentInfo.deliverList.forEach(item => {
item.show = false
})
if (this.searchValue) {
for (let i = 0; i < this.contentInfo.deliverList.length; i++) {
if (this.contentInfo.deliverList[i].name.includes(this.searchValue)) {
this.contentInfo.deliverList[i].show = true
}
}
} else {
for (let i = 0; i < this.contentInfo.deliverList.length; i++) {
this.contentInfo.deliverList[i].show = true
}
}
}
},
mounted () {
this.getAllUser()
this.initData()
},
methods: {

async initData () {
if (!this.$route.query.id) {
this.$router.push({name: 'myDealt'})
return
}

this.currentList[0] = []
this.billinfo.current_steps.forEach(item => {
this.currentList[0].push(item.name)
})
if (this.currentList[0].length === 1) {
this.formInfo.currentState[0] = this.currentList[0][0]
this.getOperate(this.currentList[0])
this.operationPlace = '请选择'
}

await this.changeForm()
},

getOperate (val) {
this.contentInfo.operatValue = ''
this.formInfo.operat = []
let temp = this.statesList.filter((item) => {
return item.name === val[0]
})
console.log(temp)
if (!temp.length) {
return
}
this.contentInfo.operatList = [[]]
this.handling = temp[0]
temp[0].operations.forEach(item => {
this.contentInfo.operatList[0].push(item.name)
})
this.operationPlace = '请选择'
},

async getAllUser () {
await this.$store.dispatch('orderProcess/getAllUser').then((res) => {
this.personList = []
res.data.forEach(item => {
this.personList.push({
name: item.name,
show: true,
check: false
})
})
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: res.data.msg
})
}).finally(() => {

})
},

async changeForm () {
this.listInfo = []
if (this.handling.fields && this.handling.fields.length) {
this.handling.fields.forEach(item => {
item.list = []
})
await this.isNecessaryToWatch(this.handling)
}
if (this.handling.fields && this.handling.fields.length) {
this.handling.fields.forEach(item => {

const valueInfo = {
name: item.name,
key: item.key,
id: item.id,
type: item.type,
choice: item.choice,
value: '',
list: item.list,
valueName: '',
desc: item.desc,
validate_type: item.validate_type,
feildCheck: false,
show_conditions: item.show_conditions,
show_type: item.show_type,
showFeild: true,
source_type: item.source_type,
source_uri: item.source_uri,
state_id: item.state_id,
ticket_id: item.ticket_id,
showValue: ''
}
if (item.source_type === 'CUSTOM') {
item.choice.forEach(ite => {
const temp = {
name: ite.name,
value: ite.key.toString(),
check: false
}
valueInfo.list.push(temp)
})
}

if (item.type === 'SELECT' || item.type === 'RADIO') {
valueInfo.value = []
}

if (item.type === 'INT' || item.type === 'STRING' || item.type === 'TEXT') {
valueInfo.desc = item.desc
}

if (item.type === 'DATE') {
valueInfo.format = 'YYYY-MM-DD'
}
if (item.type === 'DATETIME') {
valueInfo.format = 'YYYY-MM-DD HH:mm'
}

if (item.type === 'MULTISELECT' || item.type === 'CHECKBOX' || item.type === 'TREESELECT') {

valueInfo.checkStatus = false
}

if (item.type === 'TREESELECT') {

valueInfo.checkStatus = false
valueInfo.secondChildren = []
valueInfo.thirdChildren = []
}

if (item.type === 'MEMBERS') {

valueInfo.checkStatus = false

valueInfo.list = []
this.personList.forEach(node => {
valueInfo.list.push({
name: node.name,
show: true,
check: false
})
})
}
this.listInfo.push(valueInfo)
})
}
},

dispatchPerson () {
this.contentInfo.deliverList = []
if (this.handling.assignors_type === 'BY_ASSIGNOR') {

if (!this.billinfo.is_biz_need) {
this.contentInfo.groupList.splice(0, 1)
}
} else if (this.handling.assignors_type === 'PERSON' && !this.handling.assignors) {

this.contentInfo.dispatchType = 'PERSON'
this.personList.forEach(item => {
this.contentInfo.deliverList.push({
name: item,
show: true,
check: false
})
})
} else {

this.contentInfo.deliverList = []
this.contentInfo.dispatchType = 'PERSON'
const valueList = this.handling.assignors.split(',')
valueList.forEach(item => {
this.contentInfo.deliverList.push({
name: item,
show: true,
check: false
})
})
}
},

changeOperat (value) {

this.contentInfo.operatValue = value[0]
this.contentInfo.placeholder = this.contentInfo.operatValue === '打回' ? '请填写打回原因' : (this.contentInfo.operatValue === '挂起' ? '请填写挂起原因' : (this.contentInfo.operatValue === '终止' ? '请填写终止原因' : '请填写转单原因'))

this.clearInfo()

if (this.contentInfo.operatValue === '分派') {
this.dispatchPerson()
}

if (this.contentInfo.operatValue === '转单') {

this.contentInfo.deliverList = []
if (this.contentInfo.backValue.deliver_to) {
const valueList = this.contentInfo.backValue.deliver_to.split(',')
valueList.forEach(item => {
this.contentInfo.deliverList.push({
name: item,
show: true,
check: false
})
})
} else {
this.contentInfo.deliverList = this.personList
}
}
},

async checkGroup (node) {
this.contentInfo.dispatchType = node.type
if (this.contentInfo.dispatchType === 'PERSON') {
this.contentInfo.deliverList = []
this.personList.forEach(item => {
this.contentInfo.deliverList.push({
name: item.name,
show: true,
check: false
})
})
} else {
await this.getGroupInfo()
}
this.contentInfo.groupShow = false
},

async getGroupInfo () {
const params = {
role_type: this.contentInfo.dispatchType
}
await this.$store.dispatch('orderProcess/getGroupPerson', params).then((res) => {
this.contentInfo.deliverList = []
res.data.forEach(item => {
this.contentInfo.deliverList.push({
name: item.name,
info: item,
show: true,
check: false
})
})
if (this.contentInfo.deliver_to) {
this.contentInfo.deliverList.forEach(item => {
if (item.info.id === this.contentInfo.deliver_to) {
item.check = true
}
})
}
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: res.data.msg
})
}).finally(() => {

})
},

changeNode (node) {
if (this.contentInfo.operatValue === '转单') {
node.check = !node.check
this.contentInfo.deliverList = JSON.parse(JSON.stringify(this.contentInfo.deliverList))
const valueList = []
this.contentInfo.deliverList.forEach(item => {
if (item.check) {
valueList.push(item.name)
}
})
this.contentInfo.deliver_to = valueList.join(',')
this.contentInfo.deliver_name = valueList.join(',')
} else {
if (this.contentInfo.dispatchType === 'PERSON') {
node.check = !node.check
this.contentInfo.deliverList = JSON.parse(JSON.stringify(this.contentInfo.deliverList))
const valueList = []
this.contentInfo.deliverList.forEach(item => {
if (item.check) {
valueList.push(item.name)
}
})
this.contentInfo.deliver_to = valueList.join(',')
this.contentInfo.deliver_name = valueList.join(',')
} else {

const valueInfo = this.contentInfo.deliverList.some(item => item.check)
if (valueInfo) {
for (let i = 0; i < this.contentInfo.deliverList.length; i++) {
this.contentInfo.deliverList[i].check = false
}
}
node.check = !node.check
this.contentInfo.deliver_to = node.info.id
this.contentInfo.deliver_name = node.name
this.contentInfo.deliverList = JSON.parse(JSON.stringify(this.contentInfo.deliverList))
}
}
},

clearInfo () {

this.contentInfo.deliver_to = ''
this.contentInfo.deliverList = []
this.contentInfo.dispatchType = ''

this.contentInfo.reson = ''
},

showPlugin (item) {
if (item.type === 'DATE' || item.type === 'DATETIME') {
const vm = this
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: item.format,
value: item.value,
onConfirm (val) {
item.value = val
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
}

if (item.type === 'MULTISELECT' || item.type === 'CHECKBOX' || item.type === 'MEMBERS' || item.type === 'TREESELECT') {
item.checkStatus = true
}

if (item.type === 'INT' || item.type === 'STRING') {
const idName = 'input' + item.id
const elementInfo = document.getElementById(idName)
elementInfo.focus()
}
},

checkNode (item, node) {

this.contentInfo.dataMore = item.value
node.check = !node.check
this.contentInfo.deliverList = JSON.parse(JSON.stringify(this.contentInfo.deliverList))
this.handling = JSON.parse(JSON.stringify(this.handling))
const valueList = item.list.filter(x => {
return x.check
})
const valueInfo = []
valueList.forEach(x => {
valueInfo.push(x.name)
})
item.value = valueInfo.join(',')
this.handling = JSON.parse(JSON.stringify(this.handling))
},
closeMore (item) {
const valueList = this.contentInfo.dataMore.split(',')
item.value = this.contentInfo.dataMore
item.list.forEach(node => {
node.check = false
valueList.forEach(x => {
if (node.name === x) {
node.check = true
}
})
})
item.checkStatus = false
this.handling = JSON.parse(JSON.stringify(this.handling))
},
submitMore (item) {
item.checkStatus = false
this.handling = JSON.parse(JSON.stringify(this.handling))
},

submitInfo () {
if (this.contentInfo.operatValue === '提交') {
this.throughOrder()
}
if (this.contentInfo.operatValue === '认领') {
this.claimOrder()
}
if (this.contentInfo.operatValue === '分派') {
if (this.checkOther()) {
return
}
this.dispatchOrder()
}
if (this.contentInfo.operatValue === '挂起') {
if (this.checkOther()) {
return
}
this.hangOrder()
}
if (this.contentInfo.operatValue === '恢复') {
this.restoreOrder()
}
if (this.contentInfo.operatValue === '督办') {
this.superviseOrder()
}
if (this.contentInfo.operatValue === '转单') {
if (this.checkOther()) {
return
}
this.deliverOrder()
}
if (this.contentInfo.operatValue === '终止') {
if (this.checkOther()) {
return
}
this.terminateOrder()
}
},

throughOrder () {
if (this.clickSencond) {
return
}
this.clickSencond = true

if (this.checkFeild()) {
this.clickSencond = false
this.$store.commit('changetoastinfo', {
toast_state: 'cancel',
toast_text: this.toastInfo
})
this.toastInfo = ''
return
}

const fieldsList = this.listInfo.filter(item => item.showFeild).map(item => {
let tempField = {
key: item.key,
id: item.id,
type: item.type,
value: item.value,
desc: item.desc
}
if (tempField.type === 'SELECT' || tempField.type === 'RADIO') {
tempField.value = item.value[0]
if (item.source_type === 'API') {
tempField.value = tempField.value.toString()
} else if (item.source_type === 'DATADICT') {
if (tempField.value / 1) {
tempField.value = Number(tempField.value)
}
}
} else if (tempField.type === 'INT') {
tempField.value = item.value * 1
} else {
tempField.value = item.value
}
return tempField
})
const params = {
state_id: this.handling.state_id,
fields: fieldsList.map(
item => {
if (item.type === 'FILE') {
item.value = item.value.toString()
}
return {
'id': item.id,
'key': item.key,
'type': item.type,
'value': item.value
}
}
)
}
const paramsValue = {
id: this.ticketId,
params
}
this.$store.commit('changeLoadingStatus', true)
this.$store.dispatch('orderProcess/proceedOrder', paramsValue).then((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: '处理成功'
})
this.$router.push({name: 'myDealt'})
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: res.data.msg
})
}).finally(() => {
this.clickSencond = false
this.$store.commit('changeLoadingStatus', false)
})
},

checkFeild () {
console.log(this.listInfo)
const typeStatus = this.listInfo.some(item => {
return (item.type === 'TABLE' || item.type === 'RICHTEXT' || item.type === 'CUSTOMTABLE')
})
if (typeStatus) {
this.toastInfo = '抱歉，当前单据存在不支持移动端处理的字段，请移步到PC端进行处理'
return true
}
console.log(this.listInfo)
for (let i = 0; i < this.listInfo.length; i++) {
if (this.listInfo[i].showFeild) {
if (this.listInfo[i].type === 'TREESELECT') {
if (this.listInfo[i].validate_type === 'REQUIRE' && !this.listInfo[i].value) {
this.toastInfo = `请填写【${this.listInfo[i].name}】`
return true
}
} else if (this.listInfo[i].type === 'INT') {
if (this.listInfo[i].validate_type === 'REQUIRE' && !this.listInfo[i].value) {
this.toastInfo = `请填写【${this.listInfo[i].name}】`
return true
}
if (!(this.listInfo[i].value / 1)) {
this.toastInfo = `【${this.listInfo[i].name}】需填写数字`
return true
}
} else {
if (this.listInfo[i].validate_type === 'REQUIRE' && !this.listInfo[i].value.length) {
this.listInfo[i].feildCheck = true
this.toastInfo = `请填写【${this.listInfo[i].name}】`
return true
}
}
}
}
return false
},
checkOther () {
if (this.contentInfo.operatValue === '分派') {
this.contentInfo.deliverCheck = !this.contentInfo.deliver_name
return this.contentInfo.deliverCheck
}
if (this.contentInfo.operatValue === '打回' || this.contentInfo.operatValue === '挂起' || this.contentInfo.operatValue === '终止') {
this.contentInfo.reasonCheck = !this.contentInfo.reson
return this.contentInfo.reasonCheck
}
if (this.contentInfo.operatValue === '转单') {
this.contentInfo.deliverCheck = !this.contentInfo.deliver_name
this.contentInfo.reasonCheck = !this.contentInfo.reson
return (this.contentInfo.reasonCheck || this.contentInfo.deliverCheck)
}
return false
},

claimOrder () {
if (this.clickSencond) {
return
}
this.clickSencond = true
const id = this.ticketId
const params = {
state_id: this.handling.id,
processors: window.username,
processors_type: 'PERSON',
action_type: 'CLAIM'
}
this.$store.dispatch('orderProcess/distributeOrder', {params, id}).then((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'success',
toast_text: '认领成功'
})
this.$router.push({name: 'myDealt'})
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: res.data.msg
})
}).finally(() => {
this.clickSencond = false
})
},

dispatchOrder () {
if (this.clickSencond) {
return
}
this.clickSencond = true
let dispatchTo = ''
let dispatchInfo = ''
if (this.contentInfo.dispatchType === 'PERSON') {

dispatchTo = this.contentInfo.deliver_to.split(',')
const dispatchValue = []
dispatchTo.forEach(item => {
dispatchValue.push(item.split('(')[0])
})
dispatchInfo = dispatchValue.join(',')
} else {

dispatchInfo = this.contentInfo.deliver_to
}
const id = this.ticketId
const params = {
state_id: this.handling.id,
processors: dispatchInfo,
processors_type: this.contentInfo.dispatchType,
action_type: 'DISTRIBUTE'
}
this.$store.dispatch('orderProcess/distributeOrder', {params, id}).then((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: '分派成功'
})
this.$router.push({name: 'myDealt'})
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: res.data.msg
})
}).finally(() => {
this.clickSencond = false
})
},

hangOrder () {
if (this.clickSencond) {
return
}
this.clickSencond = true
const id = this.ticketId
const params = {
state_id: this.handling.id,
processors: window.username,
processors_type: 'PERSON',
action_type: 'SUSPEND',
action_message: this.contentInfo.reson
}
this.$store.dispatch('orderProcess/distributeOrder', {params, id}).then((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'success',
toast_text: '挂起成功'
})
this.$router.push({name: 'myDealt'})
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: res.data.msg
})
}).finally(() => {
this.clickSencond = false
})
},

restoreOrder () {
if (this.clickSencond) {
return
}
this.clickSencond = true
const id = this.ticketId
const params = {
state_id: this.handling.id,
processors: window.username,
processors_type: 'PERSON',
action_type: 'UNSUSPEND'
}
this.$store.dispatch('orderProcess/distributeOrder', {params, id}).then((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'success',
toast_text: '恢复成功'
})
this.$router.push({name: 'myDealt'})
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: res.data.msg
})
}).finally(() => {
this.clickSencond = false
})
},

superviseOrder () {
if (this.clickSencond) {
return
}
this.clickSencond = true
const id = this.handling.id
this.$store.dispatch('orderProcess/superviseInfo', id).then((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'success',
toast_text: '督办成功'
})
this.$router.push({name: 'myDealt'})
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: res.data.msg
})
}).finally(() => {
this.clickSencond = false
})
},

deliverOrder () {
if (this.clickSencond) {
return
}
this.clickSencond = true
let dispatchTo = ''
let dispatchInfo = ''

dispatchTo = this.contentInfo.deliver_to.split(',')
const dispatchValue = []
dispatchTo.forEach(item => {
dispatchValue.push(item.split('(')[0])
})
dispatchInfo = dispatchValue.join(',')
const id = this.ticketId
const params = {
state_id: this.handling.id,
processors: dispatchInfo,
processors_type: 'PERSON',
action_type: 'DELIVER',
action_message: this.contentInfo.reson
}
this.$store.dispatch('orderProcess/distributeOrder', {params, id}).then((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'success',
toast_text: '转单成功'
})
this.$router.push({name: 'myDealt'})
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'text',
toast_text: res.data.msg
})
}).finally(() => {
this.clickSencond = false
})
},
closeDeliver () {
this.contentInfo.deliverShow = false
},
openDeliver () {
this.contentInfo.deliverShow = true
},

terminateOrder () {
if (this.clickSencond) {
return
}
this.clickSencond = true
const id = this.ticketId
const params = {
state_id: this.handling.id,
terminate_message: this.contentInfo.reson
}
this.$store.dispatch('orderProcess/terminableOrder', {params, id}).then((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'success',
toast_text: '终止成功'
})
this.$router.push({name: 'myDealt'})
}).catch((res) => {
this.$store.commit('changetoastinfo', {
toast_state: 'success',
toast_text: '终止成功'
})
this.$router.push({name: 'myDealt'})
}).finally(() => {
this.clickSencond = false
})
},

scrollChange () {
if (this.contentInfo.operatValue === '提交') {
this.$refs.operateRef.scrollIntoView(true)
}
}
}
}



