// import ajax from '@/utils/ajax'
export default {
    data () {

    },
    created () {

    },
    watch: {},
    methods: {
        // 自定义表单数据处理
        async getFieldOptions (item) {
            let data = []
            switch (item.source_type) {
                case 'CUSTOM':
                    data = item.choice
                    break
                case 'API':
                    data = []
                    item.choice.forEach(node => {
                        data.push({
                            key: node.id,
                            name: node.name
                        })
                    })
                    break
                case 'DATADICT':
                    // 请求数据字典数据
                    await this.$store.dispatch('apiField/get_data_by_key', {
                        key: item.source_uri
                    }).then(res => {
                        data = res.data.map(item => {
                            return {
                                key: item.key,
                                view_type: item.type
                            }
                        })
                    }).catch(res => {
                        this.$bkMessage({
                            message: res.data.msg,
                            theme: 'error'
                        })
                    })
                    break
            }
            return data
        },
        // 关联数据展示的逻辑处理
        conditionField (item, list) {
            for (let i = 0; i < list.length; i++) {
                if (list[i].show_type) {
                    continue
                }
                // 对于没有expressions字段的参数，跳过逻辑处理
                if (list[i].show_conditions.expressions && list[i].show_conditions.expressions.length) {
                    for (let j = 0; j < list[i].show_conditions.expressions.length; j++) {
                        // 当前节点的类型和expressions的数据一样时，进行处理
                        // let temp = item.value[0] ? item.value[0] : item.value
                        if (item.key === list[i].show_conditions.expressions[j].key) {
                            if (list[i].show_conditions.expressions.length === 1) {
                                list[i].showFeild = this.conditionSwitch(item, list[i].show_conditions.expressions[j])
                            } else {
                                // 区分条件组‘and’和‘or’的判断逻辑
                                if (list[i].show_conditions.type === 'and') {
                                    // 判断其他字段和关联字段的关系
                                    for (let z = 0; z < list[i].show_conditions.expressions.length; z++) {
                                        if (item.key !== list[i].show_conditions.expressions[z].key) {
                                            for (let s = 0; s < list.length; s++) {
                                                if (list[i].show_conditions.expressions[z].key === list[s].key) {
                                                    list[i].showFeild = this.conditionSwitch(list[s], list[i].show_conditions.expressions[z])
                                                }
                                            }
                                        }
                                    }
                                    // 判断当前字段与关联字段的关系
                                    list[i].showFeild = list[i].showFeild ? this.conditionSwitch(item, list[i].show_conditions.expressions[j]) : list[i].showFeild
                                } else {
                                    // 判断其他字段和关联字段的关系
                                    let trueStatus = false
                                    for (let z = 0; z < list[i].show_conditions.expressions.length; z++) {
                                        if (item.key !== list[i].show_conditions.expressions[z].key) {
                                            for (let s = 0; s < list.length; s++) {
                                                if (list[i].show_conditions.expressions[z].key === list[s].key) {
                                                    list[i].showFeild = this.conditionSwitch(list[s], list[i].show_conditions.expressions[z])
                                                    if (list[i].showFeild) {
                                                        trueStatus = true
                                                    }
                                                }
                                            }
                                        }
                                    }
                                    if (trueStatus) {
                                        list[i].showFeild = true
                                    }
                                    list[i].showFeild = list[i].showFeild ? list[i].showFeild : this.conditionSwitch(item, list[i].show_conditions.expressions[j])
                                }
                            }
                            list[i].showFeild = !list[i].showFeild
                            if (!list[i].showFeild) {
                                list[i].val = list[i].value = ''
                                this.conditionField(list[i], list)
                            }
                        }
                    }
                }
            }
        },
        conditionSwitch (item, value) {
            let statusInfo = false
            let temp = typeof (item.value) === 'string' ? item.value : item.value[0]
            const typeList = ['CHECKBOX', 'MEMBERS', 'MULTISELECT']
            switch (value.condition) {
                case '==':
                    if (typeList.some(type => type === item.type)) {
                        statusInfo = temp.indexOf(value.value) !== -1
                    } else {
                        statusInfo = (temp === value.value || Number(temp) === Number(value.value))
                    }
                    break
                case '!=':
                    if (typeList.some(type => type === item.type)) {
                        statusInfo = temp.indexOf(value.value) === -1
                    } else {
                        statusInfo = (temp !== value.value && Number(temp) !== Number(value.value))
                    }
                    break
                case '>':
                    if (item.type === 'DATE' || item.type === 'DATETIME') {
                        statusInfo = this.timeStamp(temp) > this.timeStamp(value.value)
                    } else {
                        statusInfo = Number(temp) > Number(value.value)
                    }
                    break
                case '<':
                    if (item.type === 'DATE' || item.type === 'DATETIME') {
                        statusInfo = this.timeStamp(temp) < this.timeStamp(value.value)
                    } else {
                        statusInfo = Number(temp) < Number(value.value)
                    }
                    break
                case '>=':
                    if (item.type === 'DATE' || item.type === 'DATETIME') {
                        statusInfo = this.timeStamp(temp) >= this.timeStamp(value.value)
                    } else {
                        statusInfo = Number(temp) >= Number(value.value)
                    }
                    break
                case '<=':
                    if (item.type === 'DATE' || item.type === 'DATETIME') {
                        statusInfo = this.timeStamp(temp) <= this.timeStamp(value.value)
                    } else {
                        statusInfo = Number(temp) <= Number(value.value)
                    }
                    break
                case 'issuperset':
                    const issupersetList = value.value.split(',')
                    const statusList = issupersetList.map(val => temp.indexOf(val) === -1)
                    statusInfo = statusList.every(status => !status)
                    break
                case 'notissuperset':
                    const valnoList = value.value.split(',')
                    const statusnoList = valnoList.map(val => temp.indexOf(val) === -1)
                    statusInfo = !statusnoList.every(status => !status)
                    break
                default:
                    statusInfo = true
            }
            return statusInfo
        },
        // 时间字符转换成时间戳
        timeStamp (timeValue) {
            const timeInfo = timeValue.replace(/-/g, '/')
            const timeStampValue = new Date(timeInfo).getTime()
            return timeStampValue
        }
    }
}



